export default{
	data(){
		return{
			hidden:true,
			message:'',
		}
	},
	onLoad(options) {
		var a=options.message;
		var b=JSON.parse(a);
		this.message=b;
	    if(this.message.reply!=null){
			this.hidden=false
		}
	}
}
